<template>
  <script :id="randomId" name="content" type="text/plain"></script>
</template>

<script>
export default {
  name: "VueUEditor",
  props: {
    ueditorPath: {
      // UEditor 
      type: String,
      default: "/static/ue/"
    },
    ueditorConfig: {
      // UEditor 
      type: Object,
      default: function() {
        return {};
      }
    },
    ueditorId: {
      type: Number,
      default: -1
    }
  },
  data() {
    return {
      //  id
      randomId: "editor_" + Math.random() * 100000000000000000,
      instance: null,
      // scriptTagStatus -> 0:1:2:
      scriptTagStatus: 0
    };
  },
  created() {
    if (window.UE !== undefined) {
      // 
      this.scriptTagStatus = 2;
      this.initEditor();
    } else {
      // 
      this.insertScriptTag();
    }
  },
  beforeDestroy() {
    //  UEditor 
    if (this.instance !== null && this.instance.destroy) {
      this.instance.destroy();
      // this.instance.reset()
    }
  },
  methods: {
    insertScriptTag() {
      let editorScriptTag = document.getElementById("editorScriptTag");
      let configScriptTag = document.getElementById("configScriptTag");
      // tagtag
      if (editorScriptTag === null) {
        configScriptTag = document.createElement("script");
        configScriptTag.type = "text/javascript";
        configScriptTag.src = this.ueditorPath + "neditor.config.js";
        configScriptTag.id = "configScriptTag";

        editorScriptTag = document.createElement("script");
        editorScriptTag.type = "text/javascript";
        editorScriptTag.src = this.ueditorPath + "neditor.all.js";
        editorScriptTag.id = "editorScriptTag";

        const s = document.getElementsByTagName("head")[0];
        s.appendChild(configScriptTag);
        s.appendChild(editorScriptTag);
      }

      // 
      if (configScriptTag.loaded) {
        this.scriptTagStatus++;
      } else {
        configScriptTag.addEventListener("load", () => {
          this.scriptTagStatus++;
          configScriptTag.loaded = true;
          this.initEditor();
        });
      }

      if (editorScriptTag.loaded) {
        this.scriptTagStatus++;
      } else {
        editorScriptTag.addEventListener("load", () => {
          this.scriptTagStatus++;
          editorScriptTag.loaded = true;
          this.initEditor();
        });
      }
      this.initEditor();
    },
    initEditor() {
      // scriptTagStatus  2  js 
      if (this.scriptTagStatus === 2 && this.instance === null) {
        // Vue  DOM  template  script 
        //  nextTick  UEditor
        this.$nextTick(() => {
          this.instance = window.UE.getEditor(
            this.randomId,
            this.ueditorConfig
          );
          this.instance.ueditorId = this.ueditorId;
          //  UEditor  ready 
          this.instance.addListener("ready", () => {
            this.$emit("ready", this.instance);
          });
        });
        this.insertPluginScriptTab();
      }
    },
    insertPluginScriptTab() {
      // kityformula-plugin
      let kfDialogScriptTag = document.getElementById("kfDialogScriptTag");
      let kfContentScriptTag = document.getElementById("kfContentScriptTag");
      let filterFixScriptTag = document.getElementById("filterFixScriptTag");

      kfDialogScriptTag = document.createElement("script");
      kfDialogScriptTag.type = "text/javascript";
      kfDialogScriptTag.src =
        this.ueditorPath + "kityformula-plugin/addKityFormulaDialog.js";
      kfDialogScriptTag.id = "kfDialogScriptTag";

      kfContentScriptTag = document.createElement("script");
      kfContentScriptTag.type = "text/javascript";
      kfContentScriptTag.src =
        this.ueditorPath + "kityformula-plugin/getKfContent.js";
      kfContentScriptTag.id = "kfContentScriptTag";

      filterFixScriptTag = document.createElement("script");
      filterFixScriptTag.type = "text/javascript";
      filterFixScriptTag.src =
        this.ueditorPath + "kityformula-plugin/defaultFilterFix.js";
      filterFixScriptTag.id = "kfContentScriptTag";
      const s = document.getElementsByTagName("head")[0];
      s.appendChild(kfDialogScriptTag);
      s.appendChild(kfContentScriptTag);
      s.appendChild(filterFixScriptTag);
    }
  }
};
</script>



