<template>
    <el-dialog title="" width="600px" :visible.sync="quesRuleVisible" :before-close="close" @open="openFunc">
    	<div class="typeTitle">{{tipShow.title}}</div>
		  <dl>
          <dt></dt>
          <dd v-for="(item, index) in tipShow.ruleLines">{{index+1}}{{item}}</dd>
      </dl>
      <dl>
          <dt></dt>
          <dd v-for="(item, index) in tipShow.examples">{{item}}</dd>
      </dl>
    </el-dialog>
</template>
<style scoped>
  .typeTitle{text-align:center;font-size:18px;font-weight:bold;margin-top:-10px}
  dl{border:1px dashed #409EFF;padding:10px;margin-top:15px}
  dt{color:#409EFF;font-size:16px;margin-bottom:5px}
</style>
<script>
export default {
  data() {
    return {
      tipShow:{},
      tipDatas:[{
        title:"",
        ruleLines:["1.1(1)","A.A 8 ","()",""],
        examples:["1. Don'tunknown files on the Internet.","Adownloads","Bdownload","Cdown","Dload","D","not()"]
      },{
        title:"",
        ruleLines:["1.1(1)","A.A 8 ",""],
        examples:["1. The followings can beclassfied as off-balance sheet business are","ADeposits","BTrust Consulting","CL/C","DAgency","BCD","()"]
      },{
        title:"",
        ruleLines:["1.1(1","truefalse","Tab"],
        examples:["1. Everyone was in his room when the bell rings","","()"]
      },{
        title:"",
        ruleLines:["1.1(1"," ||shift+\) ","()"],
        examples:["1.We  going to sing some songs  our firends.","arewere|for","()"]
      },{
        title:"",
        ruleLines:["1.1(1",""],
        examples:["1. What is Product literature","Product literature is a primary subset of business publishing specifically geared toward the selection, purchase and use of a business' products.Typically this includes product promotionally literature, product datasheets, product operating manuals and product purchase terms and conditions.","()"]
      },{
        title:"",
        ruleLines:["1.1(1",""],
        examples:["1. ","1","A","B","C","D","D","()","2","A","B","C","D","D"," ()"]
      }]
    }
  },
  props: ["quesType","quesRuleVisible"],

  methods: {
    openFunc() {
      if (this.quesType) {
         this.tipShow = this.tipDatas[parseInt(this.quesType)-1]
      }
    },
    save() {},
    close() {
      this.$emit("closeDiolog", false);
    }
  }
};
</script>



